
@charset "UTF-8";
/* reset */
@import url(./assets/styles/animate.css);
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
textarea,
p,
blockquote,
th,
td,
input,
select,
textarea,
button {
  margin: 0;
  padding: 0;
  font-family: "Times New Roman",Georgia,Serif;
}

/* marginpadding */
fieldset,
img {
  border: 0 none;
}

/* fieldset0*/
dl,
ul,
ol,
menu,
li {
  list-style: none;
}

/* onne,menuHTML5 */
blockquote,
q {
  quotes: none;
}

/*  */
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

/* */
input,
select,
textarea,
button {
  vertical-align: middle;
  outline: medium none;
}
button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
}
address,
caption,
cite,
code,
dfn,
em,
var {
  font-style: normal;
  font-weight: normal;
}
caption {
  display: none;
}
table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
}
img {
  vertical-align: middle;
}

/*  */
::selection {
  color: #fff;
  background-color: #4C6E78;
}
::-moz-selection {
  color: #fff;
  background-color: #4C6E78;
}

/**/
.clear {
  clear: both;
}

/*--*/
.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
}
.clearfix:after {
  clear: both;
}

/*  */
html {
  font-size: 100px;
}

/*  */
body,
th,
td,
input,
select,
textarea,
button {
  font-size: 12px;
  font-family: "Times New Roman",Georgia,Serif;
}

/* body  */
body {
  text-align: center;
}
html,
body {
  height: 100%;
  font-size: .3rem;
}
.hide {
  display: none;
}
.icon {
  width: 1rem;
  height: 1rem;
  vertical-align: -0.15rem;
  fill: currentColor;
  overflow: hidden;
}

/**/
#app {
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.showlist-leave-active,
.showlist-enter-active {
  transition: all 0.5s;
}
.showlist-enter {
  opacity: 0;
  transform: translateX(100%);
}
.showlist-leave-active {
  opacity: 0;
  transform: translateX(-100%);
}
.page {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}



